hr {
  border-color: rgb(32, 90, 32);
}

@media print {
  .hidden-sm {
    display: none !important;
  }
  .hidden-xs {
    display: block !important;
  }
  .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12 {
    float: left !important;
  }
  .col-sm-12 {
    width: 100%;
  }
  .col-sm-11 {
    width: 91.66666666666666%;
  }
  .col-sm-10 {
    width: 83.33333333333334%;
  }
  .col-sm-9 {
    width: 75%;
  }
  .col-sm-8 {
    width: 66.66666666666666%;
  }
  .col-sm-7 {
    width: 58.333333333333336%;
  }
  .col-sm-6 {
    width: 50%;
  }
  .col-sm-5 {
    width: 41.66666666666667%;
  }
  .col-sm-4 {
    width: 33.33333333333333%;
  }
  .col-sm-3 {
    width: 25%;
  }
  .col-sm-2 {
    width: 16.666666666666664%;
  }
  .col-sm-1 {
    width: 8.333333333333332%;
  }
}

@media (min-width: 768px) {
  .main-panel:before {
    border-left: 1px solid #999;
    bottom: 0;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
}

#picture {
  border-radius: 100%;
  width: 100%;
  max-width: 200px;
  margin: 0 auto;
  display: block;
}

.small-caps {
  font-variant: small-caps;
}

.main-panel {
  text-align: justify;
}

.main-panel h3 {
  border-left: 2px outset rgb(32, 90, 32);
  border-right: 2px inset white;
  border-bottom: 2px inset rgb(32, 90, 32);
  border-top: 2px outset white;
  padding-left: 5px;
  color: rgb(32, 90, 32) !important;
}

.main-panel ul {
  padding-left: 20px;
}



